* {
  box-sizing: border-box;
  font-family: 'Rubik', sans-serif;
  color: #393c41;
  margin: 0;
  padding: 0;
}
body {
  overflow-x: hidden;
  width: 100%;
}

a {
  text-decoration: none;
}

@keyframes animateDown {
  0%,
  20%,
  60%,
  100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(5px);
  }
  60% {
    transform: translateY(3px);
  }
}
